.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #84d9bc;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 2;
}

[class*=-container] {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  position: relative;
  z-index: 10;
  display: inline-block;
  font-size: 3.2rem;
  color: #313c4e;
}
h2::after {
  content: "";
  z-index: -1;
  bottom: 0.5rem;
  position: absolute;
  display: block;
  width: 100%;
  height: 5px;
  background-color: #84d9bc;
}

h3 {
  font-size: 2.8rem;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.header {
  padding: 5rem 0;
  background-image: linear-gradient(to top, rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
}
.header__content {
  display: grid;
  justify-items: center;
  gap: 10rem;
}
@media (min-width: 768px) {
  .header__content {
    justify-items: start;
    gap: 8rem;
  }
}
@media (min-width: 1024px) {
  .header__content {
    gap: 15rem;
  }
}
.header__logo {
  width: 20rem;
  height: auto;
}
.header__texto {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__texto {
    align-items: flex-start;
  }
}
.header__title {
  text-align: center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__title {
    max-width: 50rem;
    text-align: start;
    font-size: 6rem;
  }
}

.btn {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.btn:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
    display: inline-block;
  }
}

.crea {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .crea {
    padding: 8rem 0;
  }
}
.crea__content {
  display: grid;
  justify-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .crea__content {
    grid-template-columns: repeat(2, 1fr);
  }
}
.crea__img {
  width: 25rem;
  height: auto;
}
@media (min-width: 768px) {
  .crea__img {
    width: 50rem;
  }
}
.crea__texto {
  max-width: 60rem;
  text-align: center;
}
@media (min-width: 768px) {
  .crea__texto {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    text-align: start;
  }
}
.crea__title {
  margin-bottom: 1rem;
}

.beneficio {
  padding: 5rem 0;
  background-color: #84d9bc;
  overflow: hidden;
}
@media (min-width: 768px) {
  .beneficio {
    padding: 8rem 0;
  }
}
.beneficio__content {
  display: grid;
  justify-items: center;
  gap: 5rem;
}
@media (min-width: 768px) {
  .beneficio__content {
    grid-auto-flow: column;
    grid-template-columns: repeat(2, 1fr);
  }
}
.beneficio__img {
  width: 28rem;
  height: auto;
}
@media (min-width: 768px) {
  .beneficio__img {
    width: 40rem;
    transition: transform 0.3s ease-in-out;
  }
  .beneficio__img:hover {
    transform: scale(1.1) rotate(-6deg);
  }
}
.beneficio__texto {
  max-width: 60rem;
  text-align: center;
}
@media (min-width: 768px) {
  .beneficio__texto {
    grid-column: 1/2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    text-align: start;
  }
}
.beneficio__title {
  margin-bottom: 1rem;
}
.beneficio__title::after {
  background-color: #FFFFFF;
}

.estadisticas {
  padding: 5rem 0;
}
.estadisticas__img {
  margin: 1rem auto 0;
  width: 50rem;
  height: auto;
}
@media (min-width: 768px) {
  .estadisticas__img {
    width: 100%;
  }
}

.planes {
  padding: 5rem 0;
  background-color: #313c4e;
}
.planes__title, .planes__descripcion {
  color: #FFFFFF;
}
.planes__flex {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .planes__flex {
    margin-top: 8rem;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
    gap: unset;
  }
}

.plan {
  width: 100%;
  max-width: 30rem;
  padding: 15rem 4rem 4rem 4rem;
  background-color: #FFFFFF;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 3rem;
  background-size: 8rem;
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .plan {
    width: unset;
    max-width: unset;
  }
  .plan:hover {
    transform: scale(1.1);
  }
}
.plan--pro {
  background-image: url("../img/icono_pro.svg");
}
.plan--inicio {
  order: 1;
  background-image: url("../img/icono_inicio.svg");
}
@media (min-width: 768px) {
  .plan--inicio {
    order: unset;
  }
}
.plan--negocio {
  z-index: 10;
  order: -1;
  background-color: #84d9bc;
  background-image: url("../img/icono_negocio.svg");
}
@media (min-width: 768px) {
  .plan--negocio {
    order: unset;
    transform: scale(1.1);
    border-radius: 1rem;
  }
  .plan--negocio:hover {
    transform: scale(1.2);
  }
}
.plan__titulo {
  text-align: center;
}
.plan__precio {
  font-size: 1.8rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}
.plan__lista {
  margin: 1rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.plan__btn {
  margin-top: 2rem;
  padding: 1rem;
  display: block;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #84d9bc;
}
.plan__btn--negocio {
  color: #84d9bc;
  background-color: #FFFFFF;
}

.testimoniales {
  padding: 5rem 0;
  display: grid;
  justify-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .testimoniales {
    grid-template-columns: 1.5fr 2fr;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .testimoniales figure {
    justify-self: end;
  }
}
.testimoniales__img {
  width: 30rem;
  height: auto;
}
.testimoniales__grid {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    grid-template-columns: repeat(13, 1fr);
    grid-template-rows: unset;
  }
}
.testimoniales__testimonial {
  max-width: 40rem;
  padding: 2rem 2rem 2rem 4rem;
  border: 1rem solid #313c4e;
  border-radius: 2rem;
  background-color: #84d9bc;
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  background-size: 2rem;
  background-position: 1rem 1rem;
}
@media (min-width: 768px) {
  .testimoniales__testimonial:nth-child(1) {
    grid-column: 1/-1;
  }
  .testimoniales__testimonial:nth-child(2) {
    grid-column: 2/-1;
  }
  .testimoniales__testimonial:nth-child(3) {
    grid-column: 3/-1;
  }
}

.footer {
  padding: 5rem 0 0;
  color: #FFFFFF;
  background-color: #313c4e;
}
.footer__content {
  display: grid;
  gap: 5rem;
}
@media (min-width: 768px) {
  .footer__content {
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__logo {
  width: 20rem;
  height: auto;
  margin: 0 auto;
}
.footer__titulo {
  color: #84d9bc;
}
.footer__navegacion {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.footer__navegacion a {
  color: #FFFFFF;
}
.footer__copy {
  margin-top: 2rem;
  padding: 2rem 0;
  border-top: 1px solid #84d9bc;
  text-align: center;
}